import axios from 'axios'
import { useEffect, useState } from 'react'
import { useParams } from 'react-router'

const DevotionDetails = () => {
  const { devotionId } = useParams()
  const [data, setData] = useState(null)
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState(null)

  useEffect(() => {
    console.log(devotionId)
    axios
      .get(`https://evangelism.thecophq.org/api/devotions/${devotionId}`)
      .then((result) => {
        console.log(result.data)
        setData(result.data)
        setError(null)
        setIsLoading(false)
      })
      .catch((err) => {
        // console.log("error")
        setError('An error occurred, please try again later')
        setIsLoading(false)
      })
  }, [devotionId])

  return (
    <div className="mx-5" style={{ marginTop: '6rem' }}>
      {error && (
        <div class="alert alert-danger" role="alert">
          <p>{error}</p>
        </div>
      )}
      {isLoading && (
        <div className="text-center">
          <div
            class="spinner-border text-info "
            role="status"
            style={{ marginTop: '60px' }}
          >
            <span class="visually-hidden">Loading...</span>
          </div>
        </div>
      )}
      {/* {console.log(data)} */}
      {data && (
        <>
          <h5 className="text-center">{data.topic}</h5>
          <div>
            <h6 className="text-center mt-1">Introduction</h6>
            <p>{data.introduction}</p>
          </div>
          <div>
            <h6 className="text-center mt-1">Questions</h6>
            <p>{data.questions}</p>
          </div>
          <div>
            <h6 className="text-center mt-1">Conclusion</h6>
            <p>{data.conclusion}</p>
          </div>
        </>
      )}
    </div>
  )
}

export default DevotionDetails
