import { useEffect, useState } from "react";
import { Row, Container, Navbar, Nav } from "react-bootstrap";


import GraphData from "./GrapgData";
import "./report.css";


const GraphsDashboad = (props) => {
  // const { user, dispatch } = useContext(Context);

    const [type, setType] = useState("daily");
    // const [area, setArea] = useState(props.area);
    const [data, setData] = useState([]);
    const area = props.area ? props.area.value : "ABUAKWA";

    useEffect(()=>{
      
      let temps =[];
      props.data.map(value=>{
        // console.log(value.area);
        if(value.area === area){
            // console.log("I am area");
            temps.push(value)
        }
      });
      // console.log(temps)
      setData(temps);
    },[area])
  return (
    <div>
      {/* {console.log(data)} */}
     {(data.length !== 0) ? <> <Row>
        <Navbar style={{ backgroundColor: "rgb(244, 245, 242)", width: "100%" }}>
          <Container >
            <Nav className="me-auto fw-bold text-secondary" >
              <Nav.Link href="#home" className="mx-3 text-secondary graph" onClick={()=>{
                  setType("daily")
              }}>
                Daily
              </Nav.Link>
              <Nav.Link href="#pie" className="mx-3 text-secondary graph" onClick={()=>{
                  setType("monthly")
              }}>
                Monthly
              </Nav.Link>
              <Nav.Link href="#line" className="mx-3 text-secondary graph" onClick={()=>{
                  setType("yearly")
              }}>
                Yearly
              </Nav.Link>
            </Nav>
          </Container>
        </Navbar>
      </Row>
      <Row>
        <GraphData data={props.data} periodType={type} area={area} />
      </Row> </>: <div className="alert alert-warning" role="alert">
          <p className="fw-bold text-dark">THere is no convert at {area} yet, Try to find some and come back for the gragh. Thanks!!!</p>
        </div>}
    </div>
  );
};

export default GraphsDashboad;
