import axios from 'axios'
import { useContext, useEffect, useState } from 'react'
import { Context } from '../context/Context'

const EvangelistCount = ({ data }) => {
  const { user, dispatch } = useContext(Context)
  const [data1, setData1] = useState([])
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState(null)
  // var temps = [];

  useEffect(() => {
    let source = axios.CancelToken.source()
    // console.log("useEffect called ",)
    countConvertByEvangelist(source)

    return () => {
      source.cancel()
    }
  }, [])

  //handling number of convert based on evangelist
  const countConvertByEvangelist = async (source) => {
    // console.log('count evangelist')
    let temps = []
    let tempEr = null
    let tempIs = true
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    }

    for (let user of data) {
      try {
        const result = await axios.get(
          `https://evangelism.thecophq.org/api/reports/converts/?_where[user.username]=${user.username}`,
          config,
          { CancelToken: source.token }
        )
        const obj = {
          evangelist: user.username,
          convertNumber: result.data.length,
        }
        temps.push(obj)
        // setIsLoading(false);
        tempIs = false
        tempEr = null
      } catch (err) {
        // console.log("there is an error");
        if (axios.isCancel(err)) {
          console.log('abortion in top evangelist')
        } else {
          tempEr = 'An error occured, please reload your page'
          tempIs = false
        }
      }
    }

    setIsLoading(tempIs)
    setError(tempEr)
    const dataSort = temps.sort(function (a, b) {
      return b.convertNumber - a.convertNumber
    })
    const finalData = dataSort.slice(0, 7)
    setData1(finalData)
  }
  return (
    <div className="card" style={{ width: '18rem' }}>
      {/* {console.log(data1.length)} */}
      {error && (
        <div className="alert alert-danger" role="alert">
          <p>{error}</p>
        </div>
      )}
      {isLoading && (
        <div className="text-center">
          <div
            className="spinner-border text-info "
            role="status"
            style={{ marginTop: '60px' }}
          >
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      )}
      {data1 && (
        <>
          <div
            className="card-header text-center"
            style={{ backgroundColor: 'teal', color: 'white' }}
          >
            <p>Evangelists with more converts</p>
          </div>
          <ul className="list-group list-group-flush">
            {data1.map((value, index) => {
              // {console.log(value)}
              return (
                <li className="list-group-item" key={index}>
                  <span className="text-left">{value.evangelist} created</span>
                  <span className="text-right">
                    {value.convertNumber} converts
                  </span>
                </li>
              )
            })}
          </ul>
        </>
      )}
    </div>
  )
}

export default EvangelistCount
