
import { useState } from "react";
import axios from "axios";
// import { Link } from 'react-router-dom';



const Register = ()=>{
 
  const [error, setError] = useState(false);
  const [username, setUsername] = useState("");
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [logUrl, setLogUrl] = useState("");
  const [success, setSuccess] = useState(false);
  const [user, setUser] = useState(null);
  const [passwordConfirm, setPasswordConfirm] = useState(null);
  const [passwordNotMatch, setPasswordNotMatch] = useState(null);
 

  const handleSubmit = async (e) => {
    //it prevents the page from refreshing
    e.preventDefault();

    if(password === passwordConfirm){
      setError(false);
    
      try {
        const res = await axios.post("https://evangelism.thecophq.org/api/auth/local/register", {
          firstName,
          lastName,
          username,
          email,
          password,
        });
        res.data && window.location.replace("/login");
        setLogUrl("https://www.google.com/");
        sendMail(firstName, lastName, email, username, password, logUrl);
      } catch (err) {
        setError(true);
      }
    }else{
      setPasswordNotMatch("Make sure password and Confirm password are the same");
    }
    
  }

  // function formValidate(){
  //     // setActivateButton(true)
  //     if(passwordConfirm !== password){
  //       console.log("match");

  //       return true;
  //     }else{
  //       console.log(" do not match");

  //       return false;
  //     }
  // }

  function sendMail(firstName, lastName, email, username, password, logUrl) {
    window.Email.send({
       SecureToken : "bbd218d4-61bd-46c1-9f0e-0e918acc9249",
       To : email,
       From : "evangelism@thecophq.org",
       Subject : `Welcome, ${firstName} ${lastName}`,
      Body: `Hello, You have been registered with the COP Evangelism Ministry. Find below login details; Username: ${username} Temporal Password: ${password}. Ensure that you update the login credentials before use. Thank you. The CoP Evangelism Team`
    }).then(

  message => setSuccess(true)  
  );
  }

    return(
      <div  style={{marginTop:"10rem"}}>
      <div className="card" style={{ width: "27rem", marginLeft:"20%"}}>
      <div className="card-body">
        <h5 className="card-title text-center fw-bold">Registration Form</h5>
           
          <form className="registerForm" onSubmit={handleSubmit}> 
          <div className="mb-1">
          <label htmlFor="exampleFormControlInput1" className="form-label">First Name</label>
          <input type="text" className="form-control" id="exampleFormControlInput1" placeholder="Enter your first name" required
            onChange={e=>setFirstName(e.target.value)}/>
          </div>
          <div className="mb-1">
          <label htmlFor="exampleFormControlInput2" className="form-label">Last Name</label>
          <input type="text" className="form-control" id="exampleFormControlInput2" placeholder="Enter your last name" required
            onChange={e=>setLastName(e.target.value)}/>
          </div>
          <div className="mb-1">
          <label htmlFor="exampleFormControlInput3" className="form-label">Username</label>
          <input type="text" className="form-control" id="exampleFormControlInput3" placeholder="Enter your username" required
            onChange={e=>setUsername(e.target.value)}/>
          </div>
          <div className="mb-1">
          <label htmlFor="exampleFormControlInput4" className="form-label">Email</label>
          <input type="email" className="form-control" id="exampleFormControlInput4" placeholder="Enter your email" required
            onChange={e=>setEmail(e.target.value)}/>
          </div>
          <div className="mb-1">
          <label htmlFor="exampleFormControlInput5" className="form-label">Password</label>
          <input type="password" className="form-control" id="exampleFormControlInput5" placeholder="Enter your password" required 
            onChange={e=>setPassword(e.target.value)}/>
          </div>

          <div className="mb-1">
          <label htmlFor="exampleFormControlInput6" className="form-label">Password Confirm</label>
          <input type="password" className="form-control" id="exampleFormControlInput6" placeholder="Confirm your password" required 
            onChange={(e)=>{setPasswordConfirm(e.target.value)}}/>
          </div>
            {passwordConfirm && (password !== passwordConfirm ) && <span className="text-danger text-center">Passwords do not match</span>}
            {passwordNotMatch && <div className="text-danger">{passwordNotMatch}</div>}
            <button className="btn btn-info" type="submit">Register</button>
            {/* <div className="text-center"><span>Already have an account? </span><Link className="ps-3" to="/admin/login">SignIn here</Link></div> */}
        </form>
        
            </div>
    </div>
    </div>   
    );
};

export default Register;