import { useEffect, useState } from 'react';
import { Line} from 'react-chartjs-2';


const GraphData = ({data, periodType, area})=>{
    const [labels, setLabels] = useState(null);
    const [dataUse, setDataUse] = useState([]);

    useEffect(()=>{
        setGraph();
        setPeriod();
    },[periodType, data]);

    const setPeriod = ()=>{
        let convets = [];
        
        for(let item of data){
            // const d1 = new Date(item.date_won);
            // console.log(d1);
            const date = new Date(item.date_won);
            let d;
            switch(periodType){
                case "daily": d = date.getDay(); break;
                // case "weekly": d = date.getDay(); break;
                case "monthly": d = date.getMonth();  break;
                case "yearly": d = date.getFullYear(); break;
            }
            console.log(d)
            d = convertDateOrDay(d);
            convets.push(d);

        }
        // console.log(convets)
        graphFinalData(convets);
    }

    const graphFinalData = (legend)=>{
        let tempData = [];
        //variables of days combination
        let countMonday = 0;
        let countTuesdat = 0;
        let countWednesday = 0;
        let countThursday= 0;
        let countFriday = 0;
        let countSaturday = 0;
        let countSunday = 0;

        //variabkes of months combination
        let countJan= 0;
        let countFeb = 0;
        let countMarch = 0;
        let countApr= 0;
        let countMay = 0;
        let countJun = 0;
        let countJul = 0;
        let countAug = 0;
        let countSep = 0;
        let countOct= 0;
        let countNov = 0;
        let countDec = 0;


        //variables of years combination
        let countFirstYear= 0;
        let countSecondYear = 0;
        let countThirdYear = 0;
        let countFouthYear= 0;
        let countFithYear = 0;
        let countSixthYear = 0;
        let countSeventhYear = 0;
        let countEigthYear = 0;
        let countNinthYear = 0;
        let countTenthYear= 0;
        

        switch(periodType){
            case "daily" : 
                        for(let item of legend){
                            if(item === "Monday"){
                                countMonday++;
                            }else if(item === "Tuesday"){
                                countTuesdat++;
                            }else if(item === "Wednesday"){
                                countWednesday++;
                            }else if(item === "Thursday"){
                                countThursday++;
                            }else if(item === "Friday"){
                                countFriday++;
                            }else if(item === "Saturday"){
                                countSaturday++;
                            }else if(item === "Monday"){
                                countSunday++;
                            }
                        }; tempData.push(countMonday);tempData.push(countTuesdat);tempData.push(countWednesday);
                            tempData.push(countThursday); tempData.push(countFriday);tempData.push(countSaturday); tempData.push(countSunday);break;
                case "monthly" : 
                        for(let item of legend){
                            if(item === "January"){
                                countJan++;
                            }else if(item === "February"){
                                countFeb++;
                            }else if(item === "March"){
                                countMarch++;
                            }else if(item === "April"){
                                countApr++;
                            }else if(item === "May"){
                                countMay++;
                            }else if(item === "June"){
                                countJun++;
                            }else if(item === "July"){
                                countJul++;
                            }else if(item === "August"){
                                countAug++;
                            }else if(item === "September"){
                                countSep++;
                            }else if(item === "Octomber"){
                                countOct++;
                            }else if(item === "November"){
                                countNov++;
                            }else if(item === "December"){
                                countDec++;
                            }
                        };  tempData.push(countJan);tempData.push(countFeb);tempData.push(countMarch); tempData.push(countApr); tempData.push(countMay);
                        tempData.push(countJun); tempData.push(countJul);tempData.push(countAug); tempData.push(countSep);
                        tempData.push(countOct);tempData.push(countNov); tempData.push(countDec);break;

                        case "yearly" : 
                        // console.log("in yearly ",legend)
                        for(let item of legend){
                            if(item == "2021"){
                                countFirstYear++;
                                // console.log("2021",item)
                            }else if(item == "2022"){
                                countSecondYear++;
                            }else if(item == "2023"){
                                countThirdYear++;
                            }else if(item == "2024"){
                                countFouthYear++;
                            }else if(item == "2025"){
                                countFithYear++
                            }else if(item == "2026"){
                                countSixthYear++;
                            }else if(item == "2027"){
                                countSeventhYear++;
                            }else if(item == "2028"){
                                countEigthYear++;
                            }else if(item == "2029"){
                                countNinthYear++;
                            }else if(item == "2030"){
                                countTenthYear++;
                            };
                        }; tempData.push(countFirstYear);tempData.push(countSecondYear); tempData.push(countThirdYear); tempData.push(countFouthYear);tempData.push(countFithYear); 
                        tempData.push(countSixthYear);tempData.push(countSeventhYear);tempData.push(countEigthYear); tempData.push(countNinthYear); tempData.push(countTenthYear); break;
        }
        // console.log(tempData);
        setDataUse(tempData);
    }

    const convertDateOrDay = (param)=>{
        if(periodType === "daily"){
            // console.log("daily")
            switch(param){
                case 0 : return "Monday";
                case 1 : return "Tuesday";
                case 2 : return "Wednesday";
                case 3 : return "Thursday";
                case 4 : return "Friday";
                case 5 : return "Saturday";
                case 6 : return "Sunday";
            }
        }else if(periodType === "monthly"){
            // console.log("monthly")
            switch(param){
                case 0 : return "January";
                case 1 : return "February";
                case 2 : return "March";
                case 3 : return "April";
                case 4 : return "May";
                case 5 : return "June";
                case 6 : return "July";
                case 7 : return "August";
                case 8 : return "September";
                case 9 : return "Octomber";
                case 10 : return "November";
                case 11 : return "December";
            }
        }else{
            return param;
        }
    }
    const setGraph = ()=>{
        switch(periodType){
            case "daily": 
                        setLabels(["Monday","Tuesday","Wednesday","Thursday","Friday","Saturday","Sunday"]);

                         break;
            case "weekly":
                        setLabels(["Monday","Tuesday","Wednesday","Thursday","Friday","Saturday","Sunday"]); break;
            case "monthly": 
                        setLabels(["January","Febriary","March","April","May","June","July","August","September","October","November","December"]); break;
            case "yearly": 
                        setLabels(["2021","2022","2023","2024","2025","2026","2027","2028","2029","2030"]); break;
        }
    }
    return(
        <>
        {console.log(dataUse)}
        <Line
          data={{
            labels: labels,
            datasets: [
              {
                label: "# of Converts",
                data: dataUse,
                backgroundColor: [
                  "rgba( 255, 99, 132, 0.2)",
                  "rgba( 54, 162, 235, 0.2)",
                  "rgba( 255, 206, 86, 0.2)",
                  "rgba( 75, 192, 192, 0.2)",
                  "rgba( 153, 102, 255, 0.2)",
                  "rgba( 255, 159, 64, 0.2)",
                ],
                borderColor: [
                  "rgba( 255, 99, 132, 0.2)",
                  "rgba( 54, 162, 235, 0.2)",
                  "rgba( 255, 206, 86, 0.2)",
                  "rgba( 75, 192, 192, 0.2)",
                  "rgba( 153, 102, 255, 0.2)",
                  "rgba( 255, 159, 64, 0.2)",
                ],
                borderWidth: 1,
              },
              {
                label: `# ${area}`,
                data: dataUse,
                backgroundColor: "yellow",
              },
            ],
          }}
          width={400}
          height={200}
          options={{
            maintainAspectRatio: false,
          }}
        /></>
    );
}

export default GraphData;